@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Concert One", cursive, serif;
  font-size: 1rem;
}

h1 {
  margin: 0 0 1rem;
  padding: 0;
  font-weight: 900;
  font-size: 2.6rem;
}

.main {
  background: url(/gh-pages/static/media/san_diego.e91053de.jpg) no-repeat center;
  background-size: cover;
  height: calc(100vh - 74px);
  box-shadow: inset 0 0 0 5px white;
}

.intro {
  background: rgba(0, 0, 0, 0.85);
  width: 100%;
  text-align: center;
  color: white;
  padding: 3rem 1rem;
}

.intro a {
  color: white;
}

.flex {
  display: flex;
}

.align-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.clean {
  margin: 0;
  padding: 0;
}

.list-clean {
  list-style: none;
}

.list-inline li + li {
  margin-left: 1rem;
}

li {
  font-size: 1.5rem;
}

.blm {
  font-size: 2rem;
  text-align: center;
  margin: 0;
}

@media screen and (min-width: 600px) {
  h1 {
    font-size: 4rem;
  }

  .blm {
    font-size: 3rem;
  }

  li {
    font-size: 1.7rem;
  }
}

